<template>
    <div id="componentePagoCompras" class="border rounded-4">
        <h3>Componente Pago Compras</h3>
        <p>RESUMEN COMPRA</p>
        <hr>
        <div class="container">
            <div class="row">
                <div class="col-md-6">
                    {{ cantidadDeItem }}
                </div>
                <div class="col-md-6">
                    <p>${{calcularTotal}}</p>
                </div>
            </div>
        </div>
        <hr>
        <label for="envios">Envio</label>
        <select name="envios" id="envios" v-on:change="costoEnvio">
            <option value="5000"> Envio express 5.000</option>
            <option value="3000"> Envio normal 3.000</option>
            <option value="10000"> Envio overnight 10.000</option>
        </select>       
        {{ pagoEnvio }}
        <hr>
        <label for="descuentos">Codigo de descuentos</label>
        <input type="text" id="descuentos" placeholder="Ingresa tu codigo" v-model="codigoDescuento"><br>
        {{ evaluarCodigo }}
        <hr>
        
        <div class="container">
            <div class="row">
                <div class="col-md-6">
                    <p>Precio total</p>
                </div>
                <div class="col-md-6">
                    <p>{{ calcularCostoFinal }}</p>
                </div>
            </div>
        </div>
        <div>
            <button class="btn colorBoton" v-on:click="aprobar" id="botonRealizar">
                Realizar compra
            </button>
        </div>
    </div>
</template>

<script>

    export default{
        name: 'componentePagoCompras',
        props:{
            cantProductDos: {
                type: Array,
                required: true
            },
            pagoTotal:{
                type: Array,
                required: true
            }
        },
      
        data: function(){
            return{
                pagoEnvio: 5000,
                codigoDescuento: '',
                desc: 0,
                costoFinal: 0,
                subTotal: 0,
                contadorClicks: 0
            }
        },
        methods: {
            costoEnvio: function(){
                this.pagoEnvio = Number(document.getElementById('envios').value)
            },
            aprobar: function(){
                this.contadorClicks++;
                if(this.contadorClicks == 1){
                    let boton = document.getElementById('botonRealizar');
                    boton.style.backgroundColor = 'green';
                    boton.style.color = 'white';
                    boton.innerText = '¿Estas seguro?'
                } else if (this.contadorClicks == 2){
                    let boton = document.getElementById('botonRealizar');
                    boton.style.backgroundColor = 'blue';
                    boton.style.color = 'white';
                    boton.innerText = '¡OK!'
                    this.contadorClicks = 0;
                    
                } 
            }
        },
        computed: {
            cantidadDeItem: function(){
                this.subTotal =  this.pagoTotal.reduce((acumulator, currentValue) => {
                    return acumulator + currentValue
                }, 0)
                return this.subTotal;
            },
            calcularTotal: function(){
                let total =  this.pagoTotal.reduce((acumulator, currentValue) => {
                    return acumulator + currentValue
                }, 0)
                return total;
            },
            evaluarCodigo: function(){
                if(this.codigoDescuento == 'JQUERY2222'){
                    console.log(this.codigoDescuento);
                    let cajaInput = document.getElementById('descuentos');
                    cajaInput.style.backgroundColor = 'red';
                    this.desc = 10000;
                    return 'CODIGO ACEPTADO'
                }
            },
            calcularCostoFinal: function(){
               this.costoFinal = this.subTotal + this.pagoEnvio - this.desc;
               return this.costoFinal;
            }
        }
    }
</script>
<style scoped>
    #componentePagoCompras{
        background-color:rgba(224, 255, 47, 0.616);
        font-weight: bold;
    }
    .colorBoton{
        background-color: brown;
        color: white;
        font-weight: bold;
    }
</style>