<template>
    <div class="container">
      <div class="row">
        <div class="col-md-8">
            <ComponenteCarroCompras
            v-bind:productos="datosPolera"
            v-bind:cantProduct="cantidadProductos"
            v-on:add="sumar"
            v-on:remove="restar"
            />
        </div>
        <div class="col-md-4">
            <ComponentePagoCompras
            v-bind:cantProductDos="cantidadProductos"
            v-bind:pagoTotal="total"
            />
        </div>
      </div>
    </div>

    
    


  
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import ComponenteCarroCompras from './components/ComponenteCarroCompras.vue';
import ComponentePagoCompras from './components/ComponentePagoCompras.vue';
export default {
  name: 'App',
  components: {
    HelloWorld,
    ComponenteCarroCompras,
    ComponentePagoCompras
},
data: function(){
  return{
    datosPolera: [
      {
        rutaPolera: 'https://falabella.scene7.com/is/image/Falabella/gsc_113000798_489801_1?wid=1500&hei=1500&qlt=70',
        tituloPolera: 'Polera',
        descripcionPolera: 'color negro',
        costoPolera: '12000'
      },
      {
        rutaPolera: 'https://falabella.scene7.com/is/image/Falabella/115837877_1?wid=1500&hei=1500&qlt=70',
        tituloPolera: 'Polera',
        descripcionPolera: 'color verde',
        costoPolera: '12000'
      },
      {
        rutaPolera: 'https://falabella.scene7.com/is/image/Falabella/gsc_119230213_2225617_1?wid=1500&hei=1500&qlt=70',
        tituloPolera: 'Polera',
        descripcionPolera: 'color morado',
        costoPolera: '16990'
      }
    ],
    cantidadProductos:[
                    0,
                    0,
                    0
                ],
    total:[
      0,
      0,
      0
    ]            
  }
},
methods:{
  sumar: function(indice){
    this.cantidadProductos [indice] += 1;
    this.total[indice] = this.cantidadProductos
    [indice] * Number(this.datosPolera[indice].costoPolera) 
  },
  restar: function(indiceDisminuir){
    if(this.cantidadProductos [indiceDisminuir] > 0){
      this.cantidadProductos [indiceDisminuir] --;
      this.total[indiceDisminuir] = this.cantidadProductos
      [indiceDisminuir] * Number(this.datosPolera[indiceDisminuir].costoPolera) 
    } else {
      this.cantidadProductos[indiceDisminuir] = 0;
      this.total[indiceDisminuir] = 0;
    }
  }
}
}
</script>

