<template>

    <div id="componenteCarroCompras" class="border rounded-4">
        <h3>Componente Carro compras</h3>
        <div class="card mb-3" v-for="(producto, index) in productos" v-bind:key="index">
            <div class="row g-0">
                <div class="col-md-4">
                    <div class="container">
                        <div class="row">
                            <div class="col-md-6">
                                <img v-bind:src="producto.rutaPolera" class="img-fluid rounded-start tamañoImagen" alt="...">
                            </div>
                            <div class="col-md-6">
                                <p>
                                    {{ producto.tituloPolera }}
                                </p>
                                <p>
                                    {{ producto.descripcionPolera }}
                                </p>
                            </div>
                        </div>
                    </div>
                    
                </div>
                <div class="col-md-4">
                        <div class="container">
                            <div class="d-flex justify-content-center">
                                <div>
                                    <button v-on:click.prevent="aumentar(index)">+</button>
                                </div>
                                <div>
                                    <input type="text" size="1" v-model="cantProduct[index]">
                                </div>
                                <div>
                                    <button v-on:click.prevent="disminuir(index)">-</button>
                                </div>
                            </div>
                        </div>
                 
                       
                        
                    
                </div>
                <div class="col-md-4">
                    <p>{{ producto.costoPolera }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default{
        name: 'componenteCarroCompras',
        props:{
            productos:{
                type: Array,
                required: true

            },
            cantProduct:{
                type: Array,
                required:true
            }
        },
        data: function(){
            return{
                
            }
        },
        methods: {
            aumentar: function(indice){
                this.$emit('add', indice)
                //this.cantidadProducto [indice] += 1;
            },
            disminuir: function(indice){
                this.$emit('remove', indice)
                
            }
        }
    }



</script>

<style scoped>

    #componenteCarroCompras{
        background-color: brown;
        color: wheat;
    }
    .tamañoImagen{
        width: 50%;
    }


</style>